import { useState } from 'react';
import { Slide } from "react-reveal";

// ================ Put you faq's data in below format ==================
const faqData = [
    {
        qs: 'What’s an NFT?',
        ans: <p>
            A NFT is a ‘Non Fungible Token”. It means it’s a unique and rare digital item. You’re able to buy it, own it and trade it.
        </p>,
        color: "#a9e0fc"
    },
    {
        qs: 'How to buy a Doodle Ape Derivatives?',
        ans: <p>
            It’s very simple ! You need to be connected to your Metamask and have some ETH on it.
        </p>,
        color: "#f5ca9e"
    },
    {
        qs: 'How much does a Doodle Ape Derivatives cost?',
        ans: <p>
            Golden Hour sale : 0.025Ξ<br>
            </br>
            Public sale : 0.035Ξ

        </p>,
        color: "#fbe88e"
    },
    {
        qs: 'How many Doodle Ape Derivatives can I mint?',
        ans: <p>
            Max 10 per wallet
        </p>,
        color: "#c5c5fa"
    },

    {
        qs: 'If I buy one, where will it be?',
        ans: <p>
            Your Doodle Ape Derivatives will be stored in your ETH address (the one with which you logged in). You’ll be able to see him on Opensea!
        </p>,
        color: "#c4fccb"
    },

]

const Faq = () => {
    const [activeFaq, setActiveFaq] = useState(null);
    return (
        <section id="faqs" className="section">
            <div className="cs_container">
                <div className="sec_heading_wrapper">
                    <Slide bottom>
                        <h1 className="sec_heading">FAQ</h1>
                    </Slide>
                </div>
                <div>
                    {faqData.map((data, i) => {
                        return (
                            <Slide key={i} bottom>
                                <div className={`faq_wrapper ${activeFaq === i ? 'active_faq' : ''}`}>
                                    <div className="faq_qs" style={{ backgroundColor: data?.color }} onClick={() => setActiveFaq(prev => prev === i ? null : i)}>
                                        <h6>{data.qs}</h6>
                                    </div>
                                    <div className="faqs_answer">
                                        {data.ans}
                                    </div>
                                </div>
                            </Slide>
                        )
                    })}
                </div>
            </div>
        </section>
    )
}

export default Faq
