import { useState, useEffect, useRef } from 'react';
import "./header.css";
// logo image
import mainLogo from "assets/images/others/logo.png";
import logoText from "assets/images/others/dad-text_500.png";
// 
import discordImg from "assets/images/others/discord.svg";
import openseaImg from "assets/images/others/opensea.svg";
import { AiOutlineTwitter } from "react-icons/ai";

const Header = () => {
  const deskHeader = useRef();
  const [menuOpen, setMenuOpen] = useState(false);


  // ========== Handle on load and on scroll window events===================
  useEffect(() => {
    let btn = document.getElementById('scrollToTop');
    let ticking;

    function doSomething(scrollPos) {

      // ==== header animation
      if (scrollPos > 204) {
        deskHeader?.current?.classList.add('affix')
      } else {
        deskHeader?.current?.classList.remove('affix')
      }

      //  show go to top button
      if (btn) {
        if (scrollPos > 500) {
          btn.classList.add('show-top')
        } else {
          btn.classList.remove('show-top')
        }
      }
    }

    // fire an event on scroll
    document.addEventListener('scroll', function (e) {
      if (!ticking) {
        window.requestAnimationFrame(function () {
          doSomething(window.scrollY);
          ticking = false;
        });
        ticking = true;
      }
    });

    // ============ remove loader on window onload ===============
    document.body.onload = () => {
      let loader = document.querySelector('.loader-wrapper');
      if (loader) {
        loader.classList.add('d-none')
      }
    };


  }, [])


  // ====== scroll/navigate to the related section ===========
  const scrollTo_Section = (sec_id) => {
    let element = document.getElementById(sec_id);
    if (sec_id === 'home') {
      window.scrollTo({ top: 0, behavior: 'smooth' });
    } else {
      if (element) {
        const y = element.getBoundingClientRect().top + window.pageYOffset;
        window.scrollTo({ top: y, behavior: 'smooth' });
      }
    }
  };

  // Empty/demo function on connect wallet button
  const connectWallet = () => {
    console.log("Connect wallet metamask")
  }
  // 
  return (
    <>
      <header ref={deskHeader} className="web-header">
        <div className="cs_container header-container">
          <div className="logo-part" onClick={() => scrollTo_Section('home')}>
            <img className="headLogo" src={mainLogo} alt="" />
            <img src={logoText} alt="" />
          </div>
          <div className="header_right">
            <div className="social_icons_wrapper">
              <a href="" target="_blank" rel="noopener noreferrer" className="social_btn">
                <AiOutlineTwitter />
              </a>
              
              <a href="" target="_blank" rel="noopener noreferrer" className="social_btn">
                <img src={openseaImg} alt="" />
              </a>
            </div>
            <button className="cs_btn" onClick={connectWallet}>Coming soon</button>
          </div>
        </div>
        <div className={`nav-shadow  ${menuOpen ? 'show-shadow' : ''}`} onClick={() => setMenuOpen(false)}></div>
      </header>
    </>
  )
}

export default Header