import aboutImg from 'assets/images/others/about.png'
import { Slide } from "react-reveal";

const About = () => {
  return (
    <section id="about" className="section">
      <div className="cs_container">
        <div className="cs_row about_row align-items-center">
          <div className="cs_col_50">
            <Slide bottom cascade>
              <div className="about_content">
                <h1>
                  Who are <br /> DADs ?
                </h1>
                <p>
                Doodle Apes Derivatives is a collection of 5.555 creatively designed generative art NFTs, bringing a new era of
                excitement to the blockchain. Our collection looks to be the next great success in the world of
                web3, bringing together a network of supporters to build a close and highly-active community.

                </p>

                <p>
                Each element of the collection has been carefully considered in order to offer a top-level experience. Our artwork has been expertly designed, and our marketing efforts sit in the hands of an experienced and successful team. We’ve spared no thought or expense in ensuring that our holders receive only the very best from Doodle Ape Derivatives.
                </p>

                <p>

                Holding a Doodle Ape Derivatives NFT, and becoming part of the community means more than just owning
                an art piece. It means solidifying your place at the very top, and claiming your place in the next
                big NFT project
                </p>

                <p>
                  Are you in ?
                </p>


              </div>
            </Slide>
          </div>
          <div className="cs_col_50">
            <div className="about_img_wrapper">
              <Slide bottom>
                <img className="img_fluid" src={aboutImg} alt="" />
              </Slide>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}

export default About