import introImg from 'assets/videos/img.png'
// 
import Counter from 'utils/Counter'
import { useState } from "react";
// 
import { Slide } from "react-reveal";

const Intro = () => {
  const [timeEnded, setTimeEnded] = useState(false);
  return (
    <section id="home" className="section">
      <div className="cs_container">
        <div className="cs_row intro_row">
          <div className="cs_col_50">
            <Slide bottom cascade>
              <div className="intro_content">
                <h1 className="text_fredoc">
                  Welcome to <br />
                  Doodle Ape Derivatives
                </h1>
                <p>
                Doodle Ape Derivatives is a collection of 5.555 programmatically generated NFTs living on the Ethereum blockchain.
                </p>
                {/* show a mint button when countdown is over */}
                {timeEnded ?
                  <>
                    <Slide bottom>
                      <h2 className="mint_avail_text">
                        644/5555
                      </h2>
                      <button className="cs_btn mint_btn text_dark bg_sec">
                        Mint
                        <select id="">
                          <option value="1">1</option>
                          <option value="2">2</option>
                          <option value="3">3</option>
                          <option value="4">4</option>
                          <option value="5">5</option>
                          <option value="6">6</option>
                          <option value="7">7</option>
                          <option value="8">8</option>
                          <option value="9">9</option>
                          <option value="10">10</option>
                        </select>
                      </button>
                    </Slide>
                  </>
                  :
                  <>
                  {/* countdown date and timer */}
                    <Slide bottom>
                      <div className="launch-date" data-aos="fade-down">
                        <h3>March 24, 2022</h3>
                      </div>
                      <div className="hero-counter" data-aos="fade-up">
                        {/* pass the time to Counter component like this march 10, 2022 12:00:00  */}
                        <Counter setTimeEnded={setTimeEnded} time="2022-03-24T20:00:00Z" />
                      </div>
                    </Slide>
                  </>}

              </div>
            </Slide>
          </div>
          <div className="cs_col_50">
            <div className="intro_video_wrapper">
              <Slide bottom>
                 
              <img className="img_fluid" src={introImg} alt="" />

              </Slide>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}

export default Intro