import "./footer.css";
import mainLogo from "assets/images/others/logo_.png";
import discordIcon from "assets/images/others/discordPink.svg";
import openseaIcon from "assets/images/others/openseaPink.svg";
import { AiOutlineTwitter } from 'react-icons/ai';

const Footer = () => {
  // navigate to the related section
  const scrollTo_Section = (sec_id) => {
    let element = document.getElementById(sec_id);
    if (sec_id === 'home') {
      window.scrollTo({ top: 0, behavior: 'smooth' });
    } else {
      if (element) {
        const y = element.getBoundingClientRect().top + window.pageYOffset;
        window.scrollTo({ top: y, behavior: 'smooth' });
      }
    }
  };
  return (
    <footer className="web_footer">
      <div className="cs_container">
        <div className="cs_row footer_row">
          {/* logo */}
          <div className="footer_logo">
            <img src={mainLogo} alt="" />
          </div>
          {/* nav links */}
          <div className="footer_links">
            <ul>
              <li><span onClick={() => { scrollTo_Section('about') }}>About</span></li>
              <li><span onClick={() => { scrollTo_Section('faqs') }}>Faq</span></li>
              <li><span onClick={() => { scrollTo_Section('home') }}>Intro</span></li>
            </ul>
          </div>
          {/* intro  */}
          <div className="footer_intro">
            <p>
              Doodle Ape Derivatives is not affiliated with any project.
            </p>
          </div>
          {/*  */}
          <div className="footer_right">
            <div className="social_icons_wrapper">
              <a href="/" target="_blank" rel="noreferrer noopener" >
                <span className="social_btn"><AiOutlineTwitter /></span>
                Twitter
              </a>
             
              <a href="/" target="_blank" rel="noreferrer noopener">
                <span className="social_btn"><img src={openseaIcon} alt="discord" /></span>
                OpenSea
              </a>
            </div>
            {/*  */}
            <div className="footer_copy_right justify-content-center align-items-center cs_row">
              <div className="footer_copy_logo">
               
              </div>
              <p>All Rights Reserved
                &copy; Copyright {new Date().getFullYear()}</p>
            </div>
          </div>
        </div>
      </div>
    </footer>
  )
}

export default Footer